@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.header {
  margin: 0 auto;
  background-color: #f9f9f9;
  height:130px;
}

.header .navbar {
  padding: 10vh 0;
  margin-right: 10vw;
  margin-left: 10vw;
}

.header img {
  width: 20vw;
}

.header .link {
  color: #333 !important;
  text-decoration: none;
  display: flex;
  align-items: flex-end;
  font-weight: bolder;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1.5rem;
}

.header #aboutLink {
  margin-right: 5vw;
}

.header .link:hover {
  color: #ff8700;
}

.header .navbar-toggler {
  background-color: #333;
}

.header .navbar-toggler-icon {
  background-color: #f9f9f9;
}
